@mixin onMobile {
  @media (min-width: 320px) and (max-width: 767.5px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: 768px) and (max-width: 1279.5px) {
    @content;
  }
}

@mixin onNotebook {
  @media (min-width: 1280px) {
    @content;
  }
}

@mixin onHighScreen {
  @media (min-height: 870px) {
    @content;
  }
}

@mixin square($size) {
  width: $size;
  height: $size;
}