*, :after, :before {
  box-sizing: border-box;
}

body, p, input, h1, ul, li, h4, h2, h5, h3 {
  margin: 0;
  padding: 0;
}

a, img, svg, button {
  cursor: pointer;
}

p, h1, h2, h3, h4, h5, div {
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
  -o-user-select: none;
}

h1, h2, h3, h4, h5, h6, p, span, a, li, td, th {
  color: #2b2925;
}

@media (prefers-color-scheme: dark) {
  h1, h2, h3, h4, h5, h6, p, span, a, li, td, th {
    color: #2b2925;
  }
}

.noSelect {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
}

.noSelect:focus {
  outline: none !important;
}

@keyframes createCross {
  from {
    transform: rotateZ(0);
  }

  to {
    transform: rotateZ(45deg);
  }
}

.page {
  scroll-behavior: smooth;
  color: #2b2925;
  font-family: Cormorant Infant, Georgia, Times New Roman, serif;
  position: relative;
}

.page__body {
  background-image: url("background-mobile.d4186673.jpg");
  background-position: -390px 804px;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__menu {
  transition: transform .4s;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu--opened {
  transform: translateX(0);
}

.page__astro-help {
  margin-bottom: 16px;
}

.container {
  margin: 0 auto;
}

@media (width >= 320px) and (width <= 767.5px) {
  .container {
    max-width: 390px;
    padding: 0 16px 24px;
  }
}

@media (width >= 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 80px;
  }

  .grid {
    grid-template-columns: repeat(12, 84.6667px);
    column-gap: 24px;
  }
}

