.burger-menu {
  cursor: pointer;
  z-index: 2;
  flex-direction: column;
  width: 40px;
  height: 40px;
  padding: 8px;
  display: flex;
  position: relative;
}

.burger-menu__opener {
  opacity: 0;
  z-index: 3;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 0;
  right: 0;
}

.burger-menu__opener[type="checkbox"]:checked ~ span:first-of-type {
  opacity: 0;
  transition: all .4s;
}

.burger-menu__opener[type="checkbox"]:checked ~ span:nth-of-type(2), .burger-menu__opener[type="checkbox"]:not(:checked) ~ span:first-of-type {
  opacity: 1;
  transition: all .4s;
}

.burger-menu__opener[type="checkbox"]:not(:checked) ~ span:nth-of-type(2) {
  opacity: 0;
  transition: all .4s;
}

.burger-menu__cross {
  opacity: 0;
  background-image: url("cross.7cb1d667.svg");
  background-position: center;
  width: 24px;
  height: 24px;
  position: absolute;
}

.burger-menu__burger {
  opacity: 1;
  background-image: url("burger.2402b75d.svg");
  background-position: center;
  width: 24px;
  height: 24px;
  position: absolute;
}

.header {
  background-image: url("main-photo.6db2e0f3.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  position: relative;
}

.header__top-actions {
  background-color: #fff9;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.header__text {
  cursor: default;
  text-align: center;
  letter-spacing: .12em;
  background-image: linear-gradient(to right, #ebe9ea, #f9f9f9);
  height: 93px;
  font-family: Cormorant Infant, serif;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

@media screen and (height >= 860px) {
  .header__text {
    height: 110px;
  }
}

.header__title {
  color: #987838;
  width: 285px;
  margin: 0 auto;
  padding: 16px 0 8px;
  font-size: 16px;
  font-style: italic;
  font-weight: 500;
  line-height: 19px;
}

@media (height >= 870px) {
  .header__title {
    padding: 20px 0 10px;
  }
}

.header__subtitle {
  color: #987838;
  font-size: 12px;
  font-style: italic;
  font-weight: 300;
  line-height: 15px;
}

.menu {
  z-index: 1;
  background-image: url("menu-background.ef033601.jpg");
  background-position-y: bottom;
  height: 100vh;
}

.menu__content {
  margin-top: 28vh;
}

.menu__list {
  letter-spacing: .16em;
  text-align: center;
  width: 108px;
  height: 172px;
  margin: 0 auto 80px;
  font-family: Cormorant Infant, Times New Roman, serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 19px;
  list-style: none;
}

.menu__item {
  margin-bottom: 32px;
}

.menu__item:last-child {
  margin-bottom: 0;
}

.menu__link {
  color: #2b2925;
  text-decoration: none;
}

.menu__social-networks {
  margin: 0 auto 176px;
}

.menu__logo {
  position: absolute;
  bottom: -2px;
  left: 50%;
  right: 50%;
  transform: translate(-50%, -50%);
}

.top-actions {
  justify-content: space-between;
  align-items: center;
  height: 87px;
  padding: 16px;
  display: flex;
}

.logo {
  cursor: pointer;
  flex-direction: column;
  align-items: center;
  width: 83px;
  height: 55px;
  display: flex;
}

.logo__image {
  width: 56px;
  height: 40px;
}

.logo__text {
  letter-spacing: .16em;
  text-align: center;
  text-transform: uppercase;
  font-family: Cormorant Infant, Times New Roman, serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 15px;
}

.social-networks {
  justify-content: space-between;
  width: 80px;
  height: 32px;
  display: flex;
}

.social-networks__instagram, .social-networks__telegram {
  width: 32px;
  height: 32px;
}

.section-header {
  margin: 64px auto 32px;
}

.section-header-text {
  letter-spacing: .04em;
  font-family: Cormorant Infant;
  font-size: 28px;
  font-weight: 600;
  line-height: 42px;
}

.section-header-left-line, .section-header-right-line {
  border: .5px solid #000;
  width: 40px;
  position: absolute;
}

.section-header-left-line {
  left: 0;
}

.section-header-right-line {
  right: 0;
}

.dropdown {
  border-bottom: 1px solid;
  border-image-source: linear-gradient(#f7eedb 0%, #dfcba3 100%);
  border-image-slice: 1;
  border-image-repeat: stretch;
  height: 72px;
  position: relative;
}

.dropdown__button {
  opacity: 0;
  position: absolute;
}

.dropdown__text {
  letter-spacing: 0;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 4px;
  padding-top: 24px;
  font-family: Cormorant Infant, Times New Roman, Times, serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
}

.dropdown__arrow-wrapper {
  width: 16px;
  margin: auto;
}

.acquaintance {
